<template>
  <div class="btob-item-wrap">
    <div class="checkbox">
      <input type="checkbox" v-model="value" @change="selectItem">
    </div>
    <div class="img">
      <img :src="verify.mall_imgurl || '/static/img/default-bg.jpg'" alt="代金券">
    </div>
    <div class="cont">
      <p><strong>{{verify.name || '商品名称'}}</strong></p>
      <p>使用码：{{verify.token}}</p>
      <p>抵扣金额：{{verify.price}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Dealer-BToB-Item',
  data () {
    return {
      value: ''
    }
  },
  props: {
    verify: Object,
    index: ''
  },
  methods: {
    selectItem () {
      this.$parent.$emit('selectedVerifyItem', this.verify.token, this.value)
    }
  }
}
</script>

<style lang="less" scoped>
.btob-item-wrap{
    padding: .6rem 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f6f6f6;
    background: #fff;
    .checkbox{
      width: 2rem;
      padding: 0 .2rem;
      height: 5.8rem;
      input {
        display: inline-block;
        width: 0;
        margin-top: 10px;
        margin-left: 5px;
        text-align: left;
        -webkit-box-sizing: border-box;
        &:before{
          content: " ";
          display: inline-block;
          width: 1.5rem;
          height: 1.5rem;
          background: #EEE;
          vertical-align: middle;
          -webkit-border-radius: 50%;
          margin-right: 10px;
          -webkit-box-sizing:border-box;
          -webkit-transition:background ease-in .5s
        }
      }
      input[type="checkbox"]:checked::before{
        background-color: rgb(53, 183, 111);
        border: 6px #EEEEEE solid;
      }
    }
    .img {
      img {
        width: 4rem;
        height: 4rem;
      }
    }
    .cont{
      p {
        margin: 0;
        padding-left: 1rem;
        font-size: 13px;
      }
    }
}
</style>



