<template>
  <div class="wrap">
    <app-header title="经销商对经销商" showBack="true" />
    <div class="main">
      <div v-if="hasVerify">
        <dealer-b-to-b-item
          v-for="(item, index) in verifyList"
          :key="index"
          :verify="item"
          :index="index"
      ></dealer-b-to-b-item>
        <mt-button v-show="hasVerify" @click="submit" type="primary" size="large" style="margin: 2rem auto;">确认</mt-button>
      </div>
      <div v-else="hasVerify">
        <h3>暂无卡券</h3>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import DealerBToBItem from './dealer-btob-item'
import {getVerificationList, changeVerification} from '../../api/maintain'
export default {
  name: 'Dealer-BToB',
  data () {
    return {
      verifyList: [],
      hasVerify: false,
      selected: []
    }
  },
  created () {
    this.getDatas()
  },
  mounted () {
    this.$on('selectedVerifyItem', (item, state) => {
      if (state) {
        this.selected.push(item)
      } else {
        for (let i = 0; i < this.selected.length; i++) {
          if (this.selected[i] === item) {
            this.selected.splice(i, 1)
          }
        }
      }
    })
  },
  methods: {
    getDatas () {
      getVerificationList(true)
        .then(data => {
          if (data.state) {
            this.verifyList = data.data.list
            if (data.data.list.length) {
              this.hasVerify = true
            } else {
              this.hasVerify = false
            }
          } else {
            this.$toast(data.errormsg || '网络错误，请重试')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    },
    submit () {
      this.$messagebox.prompt('请输入上级经销商编号或电话号码')
        .then(({value, action}) => {
          if (!value) {
            return this.$toast('请输入上级经销商编号或手机号码')
          }
          changeVerification(value, this.selected.toString())
            .then(data => {
              if (data.state) {
                this.getDatas()
                this.$toast(data.errormsg)
              } else {
                this.$toast(data.errormsg || '网络错误')
              }
            })
            .catch(err => {
              console.error(err)
              this.$toast('网络错误，请重试!')
            })
        })
        .catch(() => {
          this.$toast('您取消了操作')
        })
    }
  },
  components: {AppHeader, DealerBToBItem}
}
</script>

<style lang="less" scoped>
.wrap{
  background: #e5e5e5;
  height: 100%;
  padding: 55px 0;
  overflow-x: hidden;
  overflow-y: scroll;
  .main {
    h3 {
      text-align: center;
    }
  }
}
</style>



